@import "../../styles/colors.module";

.wrapper {
  padding: 10px;
  &__head {
    padding: 10px;
    border-bottom: 1px solid $white;
    background-color: $pink;
    border-radius: 20px 20px 0 0;
  }
  &__container {
    border: 1px solid $white;
    min-height: 100%;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
  }
}
